.socials {
  width: @mobile-width;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  gap: 40px;

  @media (min-width: @tablet-width) {
    width: @tablet-width;
    padding-top: 55px;
    padding-bottom: 55px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    padding-top: 66px;
    padding-bottom: 66px;
  }
}

.socials__logo-link {
  display: flex;
  justify-content: center;
}

.socials__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 24px;
  justify-content: center;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    height: 1px;
    left: 0;
    right: 0;
    top: -20px;
    background-color: @additional_grey;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    left: 0;
    right: 0;
    bottom: -20px;
    background-color: @additional_grey;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

.socials__link {
  display: flex;
}
